@import url('https://fonts.googleapis.com/css2?family=Fira+Sans+Condensed:ital,wght@0,400;0,500;0,600;0,700;1,400;1,500;1,600;1,700&display=swap');


* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Fira Sans Condensed', sans-serif;
  font-size: 12px;
}

input[type="number"] {
  -moz-appearance: textfield;
  appearance: textfield;
}

body {
  margin: auto;
  padding: 10px;
  width: 300px;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

#root {
  width: 100%;
  overflow: hidden;
}

.container {
  width: 100%;
  margin-top: 0.5rem;
  font-weight: 400;
}

h4 {
  text-transform: uppercase;
}

.inc-exp-container {
  display: grid;
  width: 100%;
  grid-template-columns: 1fr 1fr;
  margin: 0.5rem 0;
  padding: 0.5rem 0;
  box-shadow: 0 0px 3px rgba(128, 128, 128, 0.5);

}

.inc-exp-container > div {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.money.plus {
  color: green;
}

.money.minus {
  color: red;
}

.inc-exp-container > .income-con {
  border-right: 2px solid rgba(128, 128, 128, 0.5);
}

.transaction-list {
  padding: 0.5rem 0;
}

.transaction-list .list {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: auto;
  padding: 15px 0;
  margin: 10px 0;
  border-top: 1px solid rgba(128, 128, 128, 0.5);
}

.transaction-list .list > div {
  position: relative;
  width: 90%;
  right: 0;
  box-shadow: 0 0px 3px rgba(128, 128, 128, 0.5);
  padding: 10px 7px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin-bottom: 3px;
}

.transaction-list .list > div.minus {
  border-right: 5px solid red;
}

.transaction-list .list > div.plus {
  border-right: 5px solid green;
}

.tran-con:hover + .delete-btn {
  background-color: blue;
}

.transaction-list .list > div > button {
  position: absolute;
  width: 25px;
  height: 100%;
  border: none;
  background-color: rgba(255, 0, 0, 0.5);
  align-self: center;
  right: -35px;
  /* visibility: hidden; */
}


.add-transaction > form {
  padding: 1rem 0;
  margin: 5px 0;
  border-top: 1px solid rgba(128, 128, 128, 0.5);
}

.add-transaction > form > .form-control {
  display: flex;
  flex-direction: column;
  margin: 5px 0;
}

.add-transaction > form > .form-control > input {
  margin-top: 5px;
  padding: 5px;
  border: 1px solid rgba(128, 128, 128, 0.5);
}

.add-transaction > form > .btn {
  width: 100%;
  background-color: #1877F2;
  border: none;
  padding: 7px 0;
  margin-top: 5px;
  color: white;
}

h1 {
  font-size: 20px;
}

h2 {
  font-size: 20px;
  margin-bottom: 1.5rem;
}

h3 {
  font-size: 18px;
  font-weight: 600;
}

h4 {
  font-size: 15px;
}
